import { useState } from 'react';
import type { FunctionComponent, PropsWithChildren } from 'react';
import { useTheme } from '@mui/material/styles';

import AssistantIcon from '@mui/icons-material/Assistant';
import Box from '@mui/material/Box';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import Fab from '@mui/material/Fab';
import HomeIcon from '@mui/icons-material/Home';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

import { Page } from '../libs/schema';

const settings = [
  {
    page: Page.HOME,
    label: 'Home',
    icon: <HomeIcon />,
  },
  { page: Page.REWARDS, label: 'Scan Result', icon: <AssistantIcon /> },
];

const MainLayout: FunctionComponent<PropsWithChildren> = ({ children }: PropsWithChildren) => {
  const theme = useTheme();
  const [value, setValue] = useState<Page | null>(null);

  return (
    <Box sx={{ pb: 7 }}>
      {children}
      <Fab
        color="primary"
        sx={{
          position: 'fixed',
          bottom: theme.spacing(5),
          left: '50%',
          transform: 'translateX(-50%)',
        }}
      >
        <CameraAltIcon />
        <Typography
          variant="body1"
          sx={{
            position: 'absolute',
            top: '100%',
            mt: 0.5,
            textTransform: 'none',
          }}
        >
          Scan
        </Typography>
      </Fab>
      <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
        <BottomNavigation
          showLabels
          value={value}
          onChange={(_, newValue: Page) => {
            setValue(newValue);
          }}
        >
          {settings.map(({ page, label, icon }) => (
            <BottomNavigationAction key={page} value={page} label={label} icon={icon} />
          ))}
        </BottomNavigation>
      </Paper>
    </Box>
  );
};

export default MainLayout;
